import React from 'react'
import { connect } from 'react-redux'

import Paging from './Paging'
import Note from './Note'

class NoteList extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			page: 1,
			notesPerPage: 10,
			lastSelect: 1
		}
	}


	handleSelect = async (selectedKey) => {
		await this.setState({ lastSelect: selectedKey })
		const lastPage = Math.ceil(this.props.notes.length/this.state.notesPerPage)
		switch(selectedKey) {
			case 1:
				return this.setState({ page: 1 })
			case 2:
				if (this.state.page === 1)
					return this.setState({  })
				return this.setState({ page: --this.state.page })
			case 3:
				if (this.state.page === lastPage)
					return this.setState({})
				return this.setState({ page: ++this.state.page })
			case 4:
				return this.setState({ page: lastPage })
			default: 
				return this.setState({  })
		}
	}

	render() {
		let key = 1
		const start = (this.state.page-1)*this.state.notesPerPage
		const end = start+this.state.notesPerPage
		const notesToShow = this.props.notes.slice(start, end)
		const lastPage = Math.ceil(this.props.notes.length/this.state.notesPerPage)
		return (
			<div>
				<div>
					<Paging currentPage={this.state.page} lastPage={lastPage} handleSelect={this.handleSelect}/>
					<ul>
						{notesToShow.map(note => <li key={key++}>
						<div>
							<Note note={note} Link={this.props.Link} />
							</div></li>
						)}
					</ul>
				</div>
			<Paging currentPage={this.state.page} lastPage={lastPage} handleSelect={this.handleSelect} />
			</div>
		)
	}
}

const mapStateToProps = (store) => {
	return {
		notes: store.notes
	}
}

const ConnectedNoteList = connect(
	mapStateToProps
)(NoteList)

export default ConnectedNoteList



